*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
a {
  text-decoration: none;
}
.home_section {
  top: 73px;
position: relative;
}
.primary-button {
  background: #7fad39;
padding: 8px 10px;
border: none;
border: 0;
color: white;
font-weight: 500;
border-radius: 3px;
transition: 0.15s ease-in-out;
cursor: pointer;
display: flex;
margin-top: 0px !important;
align-items: center;
}
.primary-button:hover {
  color: #7fad39;
  background: white;
}
.bg-image {
  background-image: url('images\\farmer-holds-rice-hand.jpg');
background-position: bottom;
background-repeat: no-repeat;
background-size: cover;
height: 67vh;
width: 100%;
}
.landing_page {
  position: relative;
}
.landing_page .text {
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0, 0.4); /* Black w/opacity/see-through */
  color: white;
  font-weight: bold;
  border: 3px solid #f1f1f1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  width: 80%;
  padding: 20px;
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.landing_page .text h1 {
  font-size: 7rem;
font-weight: 400;
}
.landing_page .text h4 {
  font-size: 1.5rem;
  font-weight: 400;
  margin-bottom: 1rem;
}
